import * as yup from 'yup';

let values: Array<any> = [];

export default yup
  .object({
    type: yup
      .string()
      .oneOf((values = ['qr']))
      .meta({
        type: 'select',
        values: values,
        defaultValue: 'qr'
      }),
    padding: yup
      .number()
      .min(0)
      .max(1)
      .meta({
        type: 'switch',
        values: [0, 1],
        defaultValue: 0
      }),
    color: yup
      .string()
      .matches(/^#([0-9a-f]{3}|[0-9a-f]{6}|[0-9a-f]{8})$/i)
      .meta({
        type: 'color',
        defaultValue: '#000000'
      }),
    background: yup
      .string()
      .matches(/^#([0-9a-f]{3}|[0-9a-f]{6}|[0-9a-f]{8})$/i)
      .meta({
        type: 'color',
        defaultValue: '#ffffff'
      }),
    correctionLevel: yup
      .string()
      .oneOf((values = ['L', 'M', 'Q', 'H']))
      .meta({
        type: 'select',
        values: values,
        defaultValue: 'M'
      })
  })
  .noUnknown();
